<template>
    <page>
        <header>
            <h1>AUT-OV</h1>
        </header>
        <main class="mainPage">
            <routeGuide />
            <div style="margin-top: 20px;" class="divider"></div>
            <section class="liveRoute">
                <h1>
                    <div v-if="routesLoading" class="spinner"></div>Routes
                </h1>
                <div v-if="routesAvailable">
                    <routeOption :route="route" v-for="route in routes" :key="route.id" />
                </div>
                <div v-if="!routesAvailable && !routesError" class="noContent">
                    <p>Er zijn op dit moment geen routes beschikbaar</p>
                </div>
                <div v-if="routesError" class="noContent">
                    <p>Er is een fout opgetreden bij het ophalen van mogelijke routes. Check of je een actieve internet verbinding heb.</p>
                </div>
                <div class="dropDown">
                    <p v-if="!routeButtonsOpen" @click="routeButtonsOpen = true">
                        <span class="mdi mdi-chevron-down"></span> meer opties
                    </p>
                    <p v-else @click="routeButtonsOpen = false">
                        <span class="mdi mdi-chevron-up"></span> minder opties
                    </p>
                    <div class="dropDownContent" v-if="routeButtonsOpen">
                        <button @click="refreshRoutes()">
                            <span class="mdi mdi-refresh"></span> Refresh
                        </button>
                        <div class="halfButtons">
                            <button @click="setRouteTo('home')">
                                <span class="mdi mdi-home-variant-outline"></span>
                                Huis
                            </button>
                            <button @click="setRouteTo('work')">
                                <span class="mdi mdi-briefcase-outline"></span>
                                Werk
                            </button>
                        </div>
                    </div>
                </div>
            </section>
            <div class="divider"></div>
            <trainPasses />
            <div class="divider"></div>
            <section class="userSettings">
                <h1>Instellingen</h1>
                <input
                    type="text"
                    placeholder="Thuis address"
                    v-model="address.home.query"
                    @keyup.enter="addressQueryResult('home')"
                />
                <div class="locationQuery">
                    <div
                        class="locationQueryResult"
                        @click="chooseAddress('home', result)"
                        :class="[ result.geometry.type ]"
                        v-for="result in address.home.queryResult"
                        :key="result.id"
                    >
                        <p>{{ result.properties.search }}</p>
                    </div>
                </div>
                <input
                    type="text"
                    placeholder="Werk address"
                    v-model="address.work.query"
                    @keyup.enter="addressQueryResult('work')"
                />
                <div class="locationQuery">
                    <div
                        class="locationQueryResult"
                        @click="chooseAddress('work', result)"
                        :class="[ result.geometry.type ]"
                        v-for="result in address.work.queryResult"
                        :key="result.id"
                    >
                        <p>{{ result.properties.search }}</p>
                    </div>
                </div>
                <button @click="queryBothAddresses()">search</button>
            </section>
            <p>
                Lat: {{ pos.lat }}
                Long: {{ pos.long }}
                Route to: {{ routeTo }}
            </p>
            <div class="locationPopupScim" v-if="showGeolocationPrompt !== 'granted'">
                <div class="locationPopup">
                    <div v-if="showGeolocationPrompt === 'prompt'">
                        <img src="../assets/compass-outline.svg" alt="compass" />
                        <h1>Locatie toestemming</h1>
                        <p>Om AUT-OV te gebruiken moet je toestemming geven tot de GPS functie van uw apparaat. Wij gebruiken alleen uw locatie om onze services te leveren en wordt niet opgeslagen of gedeeld met derderen.</p>
                        <button @click="giveGeolocationPermissions()">Geef toestemming</button>
                    </div>
                    <div v-if="showGeolocationPrompt === 'denied'">
                        <img src="../assets/compass-off-outline.svg" alt="compass" />
                        <h1>Geen toestemming</h1>
                        <p>De benodigde locatie toestemming is niet gegeven op dit apparaat. Hierdoor kan u niet gebruik maken van AUT-OV.</p>
                        <button onclick="location.href = 'https://support.google.com/chrome/answer/142065?hl=en'">Geef alsnog toestemming</button>
                    </div>
                    <div v-if="showGeolocationPrompt === 'notsupported'">
                        <img src="../assets/emoticon-sad-outline.svg" alt="compass" />
                        <h1>Niet ondersteund</h1>
                        <p>Jou apparaat heeft niet de benodigde GPS functionaliteit die er nodig is voor AUT-OV. Als alternatief kan je 9292 gebruiken.</p>
                        <button onclick="location.href = 'https://9292.nl/">Gebruik 9292</button>
                    </div>
                </div>
            </div>
        </main>
    </page>
</template>

<script>
import vueChannel from "vue-channel";

import page from "@components/page.vue";
import routeOption from "@components/dashboard/routeOption.vue";
import routeGuide from "@components/dashboard/routeGuide.vue";
import trainPasses from "@components/dashboard/trainPasses.vue";

import testGoogle from "@json/testGoogle.json";

import { baseEndpoint, db } from "@js/config.js";
import serverCall from "@js/serverCall.js";
import cachedCall from "@js/cachedCall.js";
import {
    routesResultsMapping,
    routesResultsFilter,
    routesFinalMapping
} from "./dashboardRoutes.js";

function getStationCodeFromQuery(query) {
    var exactMatchAvailableAt = -1;
    var matchedStation = {};
    var queried = stationsQueryable.queryable.filter((station, index) => {
        if (station.stationName.toLowerCase() === query) {
            exactMatchAvailableAt = index;
        }

        return station.stationName.toLowerCase().includes(query);
    });

    if (exactMatchAvailableAt !== -1) {
        matchedStation = stationsQueryable.queryable[exactMatchAvailableAt];
    } else {
        matchedStation = queried[0];
    }

    return matchedStation;
}

export default {
    components: {
        page,
        routeOption,
        routeGuide,
        trainPasses
    },
    data() {
        return {
            routes: [],
            pos: {
                lat: 0,
                long: 0
            },
            address: {
                home: {
                    query: "",
                    queryResult: [],
                    pos: {
                        lat: 0,
                        long: 0
                    }
                },
                work: {
                    query: "",
                    queryResult: [],
                    pos: {
                        lat: 0,
                        long: 0
                    }
                }
            },
            routeTo: "none",
            routeButtonsOpen: false,
            routesAvailable: false,
            routesLoading: false,
            routesError: false,
            showGeolocationPrompt: "prompt"
        };
    },
    computed: {
        filteredMaterialParts() {
            return this.queryResult.materieeldelen.filter(
                part => part.materieelnummer !== undefined
            );
        },
        currentSteps() {
            var steps = this.route.directions.steps;

            var withinRangeOfSteps = steps.filter(step => {});
        }
    },
    methods: {
        setRouteTo(address) {
            this.routeTo = address;
            db.routeTo = address;

            this.refreshRoutes();
        },
        refreshRoutes() {
            this.routes = [];

            this.getRoutes();
        },
        queryBothAddresses() {
            this.addressQueryResult("home");
            this.addressQueryResult("work");
        },
        async addressQueryResult(address) {
            var query = this.address[address].query;

            if (query.length > 5) {
                var results = await cachedCall(
                    `https://1313.nl/geocoder/${query}*`,
                    { maxAge: 120000 }
                );

                var features = results.features.filter(feature => {
                    return (
                        feature.geometry.coordinates[0] !==
                            this.address[address].pos.long &&
                        feature.geometry.coordinates[1] !==
                            this.address[address].pos.lat
                    );
                });

                this.address[address].queryResult = features.map(feature => {
                    feature.id = Math.random();

                    return feature;
                });
            } else {
                this.address[address].queryResult = [];
            }
        },
        chooseAddress(address, result) {
            this.address[address].query = result.properties.search;

            this.address[address].pos.long = result.geometry.coordinates[0];
            this.address[address].pos.lat = result.geometry.coordinates[1];

            this.address[address].queryResult = [];

            db[address] = this.address[address];
        },
        performQuery() {
            var query = this.query.toLowerCase();
            this.resultsError = false;

            if (!isNaN(query)) {
                serverCall("/virtualTrain/" + this.query)
                    .then(responce => {
                        this.resultType = "train";
                        this.queryResult = Object.assign(
                            {},
                            this.queryResult,
                            responce
                        );
                    })
                    .catch(err => {
                        this.resultType = "error";
                        this.queryResult = Object.assign({}, this.queryResult, {
                            err
                        });
                    });
            } else {
                // query = query.split(" to ");
                // serverCall("/trips", {
                //     fromStation: getStationCodeFromQuery(query[0]).stationCode,
                //     toStation: getStationCodeFromQuery(query[1]).stationCode
                // }).then(responce => {
                //     console.log(responce);
                // });
            }
        },
        facilityIcon(facility) {
            if (facility === "WIFI") {
                return ["mdi-wifi"];
            } else if (facility === "TOILET") {
                return ["mdi-toilet"];
            } else if (facility === "STILTE") {
                return ["mdi-volume-off"];
            } else if (facility === "FIETS") {
                return ["mdi-bicycle"];
            } else if (facility === "STROOM") {
                return ["mdi-power-plug"];
            }
        },
        async getRoutes(recallIteration = 0) {
            this.routesLoading = true;

            if (this.routeTo === "work") {
                var pos = this.address.work.pos;
            } else {
                var pos = this.address.home.pos;
            }

            var toLatLong = pos.lat + "," + pos.long;
            var fromLatLong = this.pos.lat + "," + this.pos.long;

            if (fromLatLong === "0,0" || toLatLong === "0,0") {
                recallIteration++;

                if (recallIteration < 5) {
                    setTimeout(() => {
                        this.getRoutes(recallIteration);
                    }, 1000);
                } else {
                    this.routesLoading = false;
                }

                return;
            }

            var response;

            try {
                response = await serverCall(
                    `/trips?fromLatLong=${fromLatLong}&toLatLong=${toLatLong}`
                );
            } catch (err) {
                this.routesError = true;
                this.routesAvailable = false;
                this.routesLoading = false;
                return;
            }

            this.routesLoading = false;

            if (response.status === "ZERO_RESULTS") {
                this.routesAvailable = false;
                return;
            }

            this.routesAvailable = true;
            this.routes = response.routes.map(routesFinalMapping);
        },
        peek(index) {
            var currentIndex = this.currentStepIndex;

            this.currentStepIndex = index;

            setTimeout(() => {
                this.currentStepIndex = currentIndex;
            }, 3000);
        },
        giveGeolocationPermissions() {
            navigator.geolocation.getCurrentPosition(({ coords }) => {
                this.showGeolocationPrompt = "granted";
                this.startGeolocator();
            }, error => {
                this.showGeolocationPrompt = "denied";
            });
        },
        startGeolocator() {
            navigator.geolocation.watchPosition(({ coords }) =>
                vueChannel("pos").set(coords)
            );
            navigator.geolocation.getCurrentPosition(({ coords }) =>
                vueChannel("pos").set(coords)
            );
        }
    },
    created() {
        var dynamicRefreshLimit = 20;
        var currentRefreshCount = 0;

        if ("geolocation" in navigator) {
            if ("permissions" in navigator) {
                navigator.permissions
                    .query({ name: "geolocation" })
                    .then(({ state }) => {
                        this.showGeolocationPrompt = state;

                        if (state === "granted") {
                            this.startGeolocator();
                        }
                    });
            }
        } else {
            this.showGeolocationPrompt = "notsupported";
        }

        const loadAddress = addressName => {
            if (db[addressName]) {
                this.address[addressName] = Object.assign(
                    {},
                    this.address[addressName],
                    db[addressName]
                );
            }
        };

        loadAddress("home");
        loadAddress("work");

        if (db.routeTo) {
            this.routeTo = db.routeTo;
        }

        vueChannel("pos").receive(state => {
            this.pos.lat = state.latitude;
            this.pos.long = state.longitude;

            var directDistanceHome = Math.sqrt(
                (this.address.home.pos.lat - state.latitude) ** 2 +
                    (this.address.home.pos.long - state.longitude) ** 2
            );
            var directDistanceWork = Math.sqrt(
                (this.address.work.pos.lat - state.latitude) ** 2 +
                    (this.address.work.pos.long - state.longitude) ** 2
            );

            var lastRouteTo = this.routeTo;

            if (this.routeTo === "none") {
                if (directDistanceHome < directDistanceWork) {
                    this.routeTo = "work";
                } else {
                    this.routeTo = "home";
                }

                db.routeTo = this.routeTo;
            } else {
                if (this.routeTo === "home" && directDistanceHome < 0.0004) {
                    this.routeTo = "work";
                } else if (
                    this.routeTo === "work" &&
                    directDistanceWork < 0.0004
                ) {
                    this.routeTo = "home";
                }
            }

            if (this.routeTo !== lastRouteTo) {
                this.getRoutes();
            }
        });

        this.getRoutes();
    }
};
</script>

<style lang="scss" scoped>
@import "../scss/config.scss";

// box-shadow: 0px 0px 59px -7px rgba(128,128,128,0.8);

.noContent {
    p {
        width: 70%;
        min-width: 250px;

        display: block;
        margin: 48px auto;

        font-family: $pFont;
        font-weight: 400;
        font-size: 20px;
    }
}

h1 div.spinner {
    height: 16px;
    width: 16px;

    margin: 0;
    display: inline-block;

    background-color: #000000;
    border-radius: 50%;
    animation: 500ms spinner linear infinite;
}

@keyframes spinner {
    0% {
        transform: scale(0.9);
        opacity: 0.3;
    }

    50% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(0.9);
        opacity: 0.3;
    }
}

.inner > header {
    width: 100%;
    height: 64px;

    background-color: #000000;
    // box-shadow: 0px 0px 59px -7px rgba(128,128,128,0.8);

    h1 {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
        padding: 0px 16px;

        font-size: 24px;
        color: #ffffff;
        font-family: $hFont;
        font-weight: 700;
    }
}

main.mainPage {
    h2 {
        font-family: $hFont;
        padding: 0px 16px;
    }

    input {
        width: calc(95% - 32px);

        display: block;
        margin: 8px auto;
        padding: 8px;

        font-size: 18px;
        font-family: $hFont;
        outline: 0;
        border-radius: 5px;
        border: 2px solid #000000;
        color: #000000;
    }

    button {
        width: calc(95% - 16px);

        display: block;
        margin: 8px auto;
        padding: 12px 8px;

        font-size: 18px;
        outline: 0;
        border-radius: 5px;
        background-color: #000000;
        border: 0;
        color: #ffffff;
        cursor: pointer;
    }

    .halfButtons {
        width: calc(95% - 16px);

        display: inline-block;
        position: relative;
        left: 50%;
        transform: translateX(-50%);

        button {
            width: 50%;

            float: left;
            margin: 8px 0;

            &:first-child {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;

                & + button {
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                }
            }
        }
    }

    .divider {
        height: 0px;
        width: 100%;

        border-top: 2px dashed #cecece;
    }

    .liveRoute {
        width: 95%;

        margin: 0 auto;
    }

    section > h1 {
        margin: 16px 0;

        text-align: center;
        font-family: $hFont;
        font-weight: 400;
    }

    .stationDepartures {
        border-top: 1px solid #d3d3d3;
        border-bottom: 1px solid #d3d3d3;

        h1 {
            margin: 8px auto;

            text-align: center;
            font-family: "Roboto", sans-serif;
            font-weight: 400;
            color: #363636;
        }

        .departure {
            padding: 8px 16px;

            cursor: pointer;

            p {
                margin: 0;

                font-family: "Roboto", sans-serif;
                font-weight: 500;

                span {
                    font-weight: 700;

                    &.actualTime {
                        color: #099910;
                    }

                    &.changedOldTime {
                        color: #992609;
                        text-decoration: line-through;
                    }
                }
            }

            .info {
                color: #494949;

                &.noInfo {
                    color: #8d8d8d;
                }

                .redAlert {
                    color: #992609;
                }
            }
        }
    }
}

.locationQuery {
    width: calc(95% - 42px);

    margin: 0 auto;

    .locationQueryResult {
        padding: 16px;

        border-left: #000000 2px solid;

        &.Point {
            border-left-style: dotted;
        }

        &:first-child {
            margin-top: -8px;
        }

        p {
            margin: 0;

            font-family: $pFont;
        }
    }
}

.dropDown {
    p {
        text-align: center;
        font-family: $pFont;
    }
}

.locationPopupScim {
    height: 100%;
    width: 100%;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 4;

    background-color: #ffffffb0;

    .locationPopup {
        width: 80%;

        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding-top: 32px;
        padding-bottom: 84px;

        box-shadow: 0 0 18px #00000033;
        border-radius: 10px;
        background-color: #ffffff;

        img {
            height: 64px;

            display: block;
            margin: 0px auto;
        }

        h1 {
            width: 80%;

            margin: 16px auto;

            font-family: "Roboto Mono", monospace;
        }

        p {
            width: 80%;

            margin: 16px auto;

            font-family: "Roboto", sans-serif;
        }

        button {
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            margin: 16px 0;
        }
    }
}
</style>

<style lang="scss">
p.quickOverview .mdi {
    margin-right: 4px;

    font-size: 20px;
}
</style>